html {
  box-sizing: border-box;
  font-size: 62.5%;
  background-color: #e8e8e8;
  /* 1rem = 10px */
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  font-family: "Montserrat", sans-serif;
  font-size: 1.6rem;
  height: 100%;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 2.4rem;
}

h3 {
  font-size: 1.6rem;
}

p {
  font-size: 1.4rem;
  margin: 0;
  padding: 0;
}

a {
  text-decoration: none;
  font-size: 1.2rem;
}

img {
  max-width: 100%;
  display: block;
  height: auto;
}

.btns__App {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 3rem;
  margin-top: 5rem;
}

.btn__App {
  background-color: #000;
  width: 25rem;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 1rem;
  padding: 1rem 0;
}
.btn__logo img {
  width: 3rem;
  margin-right: 1rem;
}
.btn__texto p {
  margin: 0;
  color: #fff;
  font-size: 1.8rem;
  line-height: 1;
}
.btn__texto span {
  font-size: 1rem;
}

@media (min-width: 768px) {
  .btn__App {
    width: 40rem;
  }
  .app__contenido h2 {
    margin: 0;
    font-size: 5rem;
    margin-bottom: 1.5rem;
  }
  .app__contenido p {
    font-size: 3rem;
  }
  .btn__logo img {
    width: 5rem;
    margin-right: 1rem;
  }
  .btn__texto p {
    margin: 0;
    color: #fff;
    font-size: 2.5rem;
    line-height: 1;
  }
  .btn__texto span {
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  .btns__App {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 3rem;
    margin-top: 5rem;
  }
}
.campo {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
  margin-top: 2rem;
}

input {
  padding: 1rem;
  outline: #707070 1px solid;
  border-radius: 0.5rem;
  border-style: none;
}

input[type=file] {
  width: 100%;
}

select, textarea {
  padding: 1rem;
  outline: #707070 1px solid;
  border-radius: 0.5rem;
  border-style: none;
}

input[type=checkbox] {
  outline: none;
}

input:hover {
  outline: #0a7bec 1px solid;
}

input:focus {
  outline: #0a7bec 1px solid;
}

input::placeholder {
  color: #707070;
  font-size: 1.8rem;
}

@media (min-width: 1024px) {
  .campo {
    margin-top: 1rem;
  }
  input {
    padding: 0.5;
  }
}
