  background-color: #0C6432;
  font-size: 1.4rem;
  padding: 1.3rem 2rem;
  font-weight: 400;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 0.5rem;
  font-family: "Montserrat", sans-serif;
}
.boton__verde:hover {
  cursor: pointer;
  background-color: #07361b;
}
@media (min-width: 768px) {
  .boton__verde {
    width: auto;
    display: inline-block;
  }
}

.boton__negro {
  background-color: #13161C;
  font-size: 1.4rem;
  padding: 1.3rem 2rem;
  font-weight: 400;
  display: block;
  width: 100%;
  text-align: center;
  color: #fff;
  text-decoration: none;
  transition: background-color 0.3s ease;
  border-radius: 0.5rem;
  font-family: "Montserrat", sans-serif;
}
.boton__negro:hover {
  cursor: pointer;
  background-color: black;
}
@media (min-width: 768px) {
  .boton__negro {
    width: auto;
    display: inline-block;
  }
}

.boton__blanco {
  background-color: #fff;
  font-size: 1.4rem;
