    background-image: url("../img/bg-principalM.webp");
  }
}
@media (min-width: 768px) {
  .bienvenida__contenedor {
    background-image: url("../img/bg-principalT.png");
    background-size: cover;
  }
  .bienvenida__texto {
    margin-top: -8rem;
  }
  .bienvenida__texto p {
    font-size: 3rem;
  }
  .bienvenida__texto h1 {
    font-size: 4.5rem;
  }
}
@media (min-width: 1024px) {
  .bienvenida__contenedor {
    background-image: url("../img/bg-principañ.png");
    background-size: cover;
    grid-template-rows: unset;
    grid-template-columns: repeat(2, 60% 40%);
