.card {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center bottom;
  border-radius: 1rem;
  position: relative;
  height: 20rem;
  color: #fff;
  transition: all 0.45s ease;
}
.card__des {
  color: #fff;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  align-items: center;
  z-index: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  opacity: 0;
  z-index: 2;
  transition-delay: 0.1s;
  font-size: 1.2rem;
  font-weight: 400;
  letter-spacing: 1px;
  text-transform: uppercase;
}

.card::before,
