  counter-reset: button-counter;
}

.row {
  display: table-row;
}

.toggle-button-cover {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  width: 100%;
  height: 140px;
  box-sizing: border-box;
}

.button-cover {
  width: 250px;
  height: 55px;
  margin: 0 auto;
  /* margin: 20px; */
  background-color: transparent;
  border: #707070 solid 1px;
  box-shadow: 0 10px 20px -8px #c5d6d6;
  border-radius: 5px;
}

.button-cover:before {
  counter-increment: button-counter;
  /* content: counter(button-counter); */
  position: absolute;
  right: 0;
  bottom: 0;
  /* color: #d7e3e3; */
  font-size: 12px;
  line-height: 1;
  /* padding: 5px; */
}

.button-cover,
.knobs,
.layer {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
}

.button {
  position: relative;
  top: 50%;
  width: 95%;
  height: 35px;
  margin: -20px auto 0 auto;
  overflow: hidden;
}

.button.r,
.button.r .layer {
  border-radius: 100px;
}

.button.b2 {
  border-radius: 5px;
}

.checkbox {
  position: relative;
  width: 100%;
  height: 100%;
  padding: 0;
  margin: 0;
  opacity: 0;
  cursor: pointer;
  z-index: 3;
}

.knobs {
  z-index: 2;
}

.layer {
  width: 100%;
  /* background-color: #ebf7fc;s */
  transition: 0.3s ease all;
  z-index: 1;
}

/* Button 10 */
#button-10 .knobs:before,
#button-10 .knobs:after,
#button-10 .knobs span {
  position: absolute;
  top: 4px;
  width: 50%;
  height: 55px;
  font-size: 18px;
  font-weight: bold;
  text-align: center;
  line-height: 1;
  padding: 9px 4px;
  border-radius: 5px;
  transition: 0.3s ease all;
  font-family: var(--fuenteSec);
}

#button-10 .knobs:before {
  content: "";
  left: 4px;
  background-color: #13161C;
}

#button-10 .knobs:after {
  content: "Anual";
  right: 4px;
  color: #4e4e4e;
}

#button-10 .knobs span {
  display: inline-block;
  left: 4px;
  color: #fff;
  z-index: 1;
}

#button-10 .checkbox:checked + .knobs span {
  color: #4e4e4e;
}

#button-10 .checkbox:checked + .knobs:before {
  left: 50%;
  background-color: #13161C;
}

#button-10 .checkbox:checked + .knobs:after {
  color: #fff;
}

#button-10 .checkbox:checked ~ .layer {
  /* background-color: #fcebeb; */
}

